import { Box, Typography } from "@mui/material";
import React, { useContext } from "react";
import { useNavigate } from "react-router-dom";
import { appContext } from "../../context";

const FooterOnPage = () => {
    //eslint-disable-next-line
    const history = useNavigate();

    const {theme, setTheme} = useContext(appContext);

    const switchTheme = () => {
        if (theme) {
            setTheme(false)
            return
        }
        if (theme === false) {
            setTheme(true)
            return
        }
    }

    return (
        <Box component="footer" sx={{ mb: 2 }}>
            <Box className="flex-column gap-0 note mt-1 aic_f pf">
                {/* <Typography variant="caption" className="flex-row wfull note">
                        
                </Typography> */}
                <Box className="flex-row gap-1 wrap">
                    <Box className="flex-row">
                        <Typography variant="subtitle1">
                            {/* created by&nbsp;
                            <a href="https://github.com/roso1nik" target="_blank" rel="noreferrer noopener">
                                roso1nik
                            </a> */}
                            {/* &nbsp;with love to&nbsp; */}
                        </Typography>
                        {/* <Typography variant="subtitle1" className="color-love">
                            you
                        </Typography> */}
                    </Box>
                    {/* <Typography variant="subtitle1"></Typography>
                    <Box>
                        <Typography 
                            variant="subtitle1" 
                            sx={{ textDecoration: 'underline 1px dashed', cursor: 'pointer' }}
                            onClick={() => switchTheme()}
                        >
                            {theme
                                ?   ''
                                :   ''
                            }
                            &nbsp;
                        </Typography>
                    </Box> */}
                </Box>
            </Box>
        </Box>
    )
}

export default FooterOnPage;